                          {{ item.name }}
                        </figcaption>
                      </figure>
                      <button class="btn-destinos uppercase font-DIN-Bold shadow-h-blue-md bg-orange text-2xl text-white rounded-75 px-8 py-1                                                    
                          md:px-16 md:mx-auto mx-4"
                        @click="goSinglePole(item.keyword)"
                      >
                        descubre
                      </button>
                    </div>                     
                  </SplideSlide>                     
               </Splide>
            <!-- <figure class="flex relative w-70x100
