              </figcaption>
            </figure> -->            
          </div>
        </div>
      </div>

      <figure>
        <img src="@/assets/img/home-wave-bottom.png" alt="" />
      </figure>
    </section>

    <!-- offerts -->
    <section class="bg-white py-20 px-3 sm:px-10">
      <h2 class="text-3xl py-6 text-darkBlue 
      md:text-6xl
      lg:text-4xl">- Destinos -</h2>
    
      <div
        class="flex flex-col justify-center flex-wrap gap-6 px-4
          sm:flex-row sm:px-10"
      >
        <div class="md:max-w-18rem" v-for="(item, index) in offerts" :key="index">
          <figure class="pb-3">
            <img class="rounded-t-2xl" :src="item.img" :alt="item.pole" />
          </figure>
          <div
            v-match-heights="{el: ['p'], disabled: ['200',200]}"
            class="border-dashed border-2 rounded-b-2xl border-blue px-5 py-3"
          >
            <h3 class="text-4xl text-blue uppercase font-black font-DIN-Medium">
              {{ item.name }}
            </h3>
            <span class="font-Clarissa text-blue text-3xl">{{
              item.country
            }}</span>

            <span class="uppercase block font-DIN-Bold text-blue font-black">desde</span>

            <span class="price block font-DIN-Bold text-orange text-5xl leading-3 font-bold pt-4">${{ item.price }}</span >
            <p class="offer-description text-blue bg-blueGray py-3 pt-6 px-4">
              {{ item.description }}
            </p>

            <small>{{ item.info }}</small>
          </div>
          <button class="btn-booking font-DIN-Bold w-full text-2xl my-5"
          @click="goOffer(item.keyword, item)">{{ item.btn }}
          </button>
        </div>
      </div>
    </section>

    <!--about us  -->
    <section class="about text-white relative md:block hidden">
      <img class="relative" :src="about_img" alt="" />
      <div class="flex flex-col w-full top-6r items-center absolute">
        <h2 class="uppercase text-xl py-6 text-white sm:text-2xl md:text-6xl font-DIN-Medium">
          - bienvenido a iris travel -
        </h2>
        <p class="text-justify font-DIN text-xl w-70x100" v-html="about_text">          
        </p>
      </div>
    </section>
  </div>
</template>
<script>
import config from '@/config';

import { Splide, SplideSlide } from '@splidejs/vue-splide';
import '@splidejs/splide/dist/css/themes/splide-default.min.css';

import Searcher from "@/components/searcher/Searcher.vue";
import HomeService from "@/services/home.service";
import textServices from "@/services/common/texts.service";
import "@/assets/css/slide-home.css";

export default {
  name: "Home",
  data() {
    return {
      opt_slide: {    
          direction   : 'ltr',                                                   
          rewind      : true,
          wheel       : true,
          pagination  : false,
          autoplay    : false,
          height      : 'auto',
          width       : '200rem',
          fixedWidth  : '30rem',
          fixedHeight  : '50rem',
          breakpoints: {            
            640: {
              height: 'auto',
              width: '270px',
              fixedWidth  : '30rem',
              fixedHeight  : '10rem',              
            },
          }   
      },
      poles: [
        {
          img: config.gallery_url + 'general/destinos1.png',
          name: "Cuba",          
          keyword: "cuba",          
        },
        {
          img: config.gallery_url + 'general/destinos2.png',
          name: "Cancun",
          keyword: "RQ",          
        },
        {
          img: config.gallery_url + 'general/destinos3.png',
          name: "Punta Cana",
          keyword: "RQ",          
        },
      ],
      offerts: [],   
      about_img : config.gallery_url + 'general/quienessomos-imagen.png',
      about_text: ''
    };
  },  
  created() {
    const init = []
    init.push(textServices.getText('about-home'))
    init.push(HomeService.getOffers())
    Promise.all(init).then(res => {
      if (res) {
        this.about_text = (res[0]) ? res[0].description.replace(/\r?\n/g, "<br>") : ''     
        this.offerts = res[1]     
      }
    })
  },
  components: {
    Searcher,
    Splide,
    SplideSlide
  },   
  methods: {
     goSinglePole(keyword, params) {  
      if(keyword == 'RQ') {
        this.sendRequest(params)
      } else {
        this.$router.push({
          name: 'SinglePole', 
          params: {keyword}
        })
      } 
     },
     goOffer(keyword, params) {  
      if(keyword == 'RQ') {
        this.$router.push({name: 'SendRequest'})
      } else {
        this.$router.push({
          name: 'SinglePole', 
          params: {keyword}
        })
      } 
     },
     sendRequest(params) {
        this.$router.push({name: 'SendRequest'})
     }
  }
};
</script>
<style scoped>
