  font-size: 3rem;
  font-family: Clarissa;
  top: calc(50% - 100px) !important;
  /* background: rgba(0,0 ,0 ,0.6 ); */
}
.price {
    text-shadow: -2px 2px #232E6E;
}
.about .container{
  top: 80px;
  left: 10em;
}

